import { getBlobFromURL } from './getBlobFromURL';
import { isDataUrl, toDataURL, getMimeType } from './util';
var URL_REGEX = /url\((['"]?)([^'"]+?)\1\)/g;
export function shouldEmbed(string) {
  return string.search(URL_REGEX) !== -1;
}
export function embedResources(cssString, baseUrl, options) {
  if (!shouldEmbed(cssString)) {
    return Promise.resolve(cssString);
  }

  return Promise.resolve(cssString).then(parseURLs).then(function (urls) {
    return urls.reduce(function (done, url) {
      return done.then(function (ret) {
        return embed(ret, url, baseUrl, options);
      });
    }, Promise.resolve(cssString));
  });
}
export function parseURLs(str) {
  var result = [];
  str.replace(URL_REGEX, function (raw, quotation, url) {
    result.push(url);
    return raw;
  });
  return result.filter(function (url) {
    return !isDataUrl(url);
  });
}
export function embed(cssString, resourceURL, baseURL, options, get) {
  var resolvedURL = baseURL ? resolveUrl(resourceURL, baseURL) : resourceURL;
  return Promise.resolve(resolvedURL).then(function (url) {
    return get ? get(url) : getBlobFromURL(url, options);
  }).then(function (data) {
    return toDataURL(data, getMimeType(resourceURL));
  }).then(function (dataURL) {
    return cssString.replace(urlToRegex(resourceURL), "$1".concat(dataURL, "$3"));
  }).then(function (content) {
    return content;
  }, function () {
    return resolvedURL;
  });
}

function resolveUrl(url, baseUrl) {
  // url is absolute already
  if (url.match(/^[a-z]+:\/\//i)) {
    return url;
  } // url is absolute already, without protocol


  if (url.match(/^\/\//)) {
    return window.location.protocol + url;
  } // dataURI, mailto:, tel:, etc.


  if (url.match(/^[a-z]+:/i)) {
    return url;
  }

  var doc = document.implementation.createHTMLDocument();
  var base = doc.createElement('base');
  var a = doc.createElement('a');
  doc.head.appendChild(base);
  doc.body.appendChild(a);

  if (baseUrl) {
    base.href = baseUrl;
  }

  a.href = url;
  return a.href;
}

function urlToRegex(url) {
  return new RegExp("(url\\(['\"]?)(".concat(escape(url), ")(['\"]?\\))"), 'g');
}

function escape(url) {
  return url.replace(/([.*+?^${}()|\[\]\/\\])/g, '\\$1');
}